
.main-footer{
    background-color: #24262b;
    padding:70px 0;
    margin-top: auto;
}
.main-footer *{
    margin:0;
    padding:0;
    box-sizing: border-box;
    
}
.main-footer ul{
    list-style: none;
}
.footer-row{
    display: flex;
    flex-wrap: wrap;
}
.footer-container{
    max-width: 1170px;
   
    margin: auto;
}
.footer-col{
    width: 25%;
    padding:0 15px ;
}

.footer-col h4{
    font-size: 18px;
    color:#ffffff;
    text-transform: capitalize;
    margin-bottom: 35px;
    font-weight: 500;
    position: relative;
}

.footer-col h4::before{
    content:'';
    position:absolute;
    left: 0;
    bottom: -10px;
    background-color: #e91e63;
    height:2px;
    box-sizing: border-box;
    width: 50px;
}

.footer-col ul li:not(:last-child){
    margin-bottom: 10px;
}

.footer-col ul li a{
    font-size: 16px;
    text-transform: capitalize;
    color: #ffffff;
    text-decoration: none;
    font-weight: 300;
    color:#bbbbbb;
    display: block;
    transition: all 0.3s ease;
}
.footer-col ul li:hover{
    color:#ffffff;
    padding-Left:10px;
}

.footer-col .social-links li{
    display: inline-block;
    height: 40px;
    width: 40px;
   
    margin:0 10px 10px 0;
    text-align: center;
    line-height: 40px;
    border-radius:50%;

}

.footer-col .social-links li{
    display: inline-block;
    height: 40px;
    width: 40px;
   
    margin:0 10px 10px 0;
    text-align: center;
    line-height: 40px;
    border-radius:50%;
    transition: all 0.5s ease;

}
.footer-col .social-links li:hover{
    color:#24262b;
    background-color: #ffffff;
}

@media(max-width:767px) {
    .footer-col{
        width: 50%;
        margin-bottom: 30px;
    }
}
