.box{
    margin:2rem 2rem 2rem 0;
    padding: 1.5rem;
    width:250px;
    box-shadow: 0 0 28px 4px rgba(0,0,0,1);
    transition: transform .3s ease;
    cursor: pointer;
}

.box:hover{
    transform: scale(1.1);

}

.box__title{
    font-weight:800;
    font-size:1.5rem;
}

.box__image{
    display: flex;
    justify-content: flex-end;
}

.box__image img{
    border-radius: 50%;
}